@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
body {
  font-size: 16px;
  line-height: 24px;
  background-color: #ffffff;
  color: #000000;
  margin: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}
.container {
  width: 280px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
    padding: 0 30px;
  }
}
@media (min-width: 1300px) {
  .container {
    width: 1220px;
    padding: 0 40px;
  }
}
.button:hover,
.button:active {
  background: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.additional {
  margin: 0 auto;
  padding-bottom: 35px;
  position: relative;
}
@media (min-width: 768px) {
  .additional {
    padding-bottom: 75px;
  }
}
.additional::before {
  content: "";
  position: absolute;
  background-color: #d9d9d9;
  width: 100%;
  height: 1px;
  top: 0;
  left: 0;
}
@media (min-width: 768px) {
  .additional::before {
    content: none;
  }
}
.additional::after {
  content: "";
  position: absolute;
  background-color: #68b738;
  height: 2px;
  width: 100%;
  top: 40px;
  left: 0;
  z-index: 1;
}
@media (min-width: 768px) {
  .additional::after {
    content: "";
    top: 22px;
  }
}
.additional__wrapper {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .additional__wrapper {
    width: 708px;
  }
}
@media (min-width: 1300px) {
